class Process {
  constructor(strings, index) {
    this.arrivalTime = parseInt(strings[0]);
    this.duration = parseInt(strings[1]);
    this.index = index + 1;
    this.waitingTime = 0;
  };

  wait() {
    this.waitingTime++;
  };

  doWork() {
    this.duration--;
  }
}

const algorithms = {
  maxTime: 50,

  decodePattern: (pattern) => {
    let split = pattern.split(';');
    return split.map((value, index) => new Process(value.split(","), index));
  },


  fcfs: (pattern) => {
    let processes = algorithms.decodePattern(pattern);

    let chosenProcess;
    let arrivedProcesses = [];
    let completedProcesses = [];

    let workHistory = [];

    for (let i = 0; i <= algorithms.maxTime; i++) {

      // Wait all processes in queue
      for (const arrivedProcess of arrivedProcesses) {
        arrivedProcess.wait();
      }

      // Do work for the currently selected process
      if (chosenProcess) {
        chosenProcess.doWork();
        workHistory.push(chosenProcess.index);
      } else {
        workHistory.push(-1);
      }

      // Process is completed
      if (chosenProcess && chosenProcess.duration === 0) {
        completedProcesses.push(chosenProcess);
      }

      // No more work needs to be done
      if (processes.length === 0 && arrivedProcesses.length === 0 && chosenProcess && chosenProcess.duration === 0) {
        break;
      }

      // Save the processes arriving at current time
      let len = processes.length;
      for (let j = 0; j < len; j++) {
        if (processes[0].arrivalTime === i) {
          arrivedProcesses.push(processes[0]);
          processes.shift();
          len--;
        } else {
          break;
        }
      }


      // Choose a new process
      if (!chosenProcess || chosenProcess && chosenProcess.duration === 0) {
        if (arrivedProcesses.length > 0) {
          chosenProcess = arrivedProcesses[0];
          arrivedProcesses.shift();
        } else {
          chosenProcess = undefined;
        }
      }

    }

    return {
      name: 'FCFS',
      workHistory: workHistory,
      avgWaitTime: completedProcesses.map(value => value.waitingTime)
        .reduce((previousValue, currentValue) => previousValue + currentValue) / completedProcesses.length
    };
  },


  srtf: (pattern) => {
    let processes = algorithms.decodePattern(pattern);

    let chosenProcess;
    let arrivedProcesses = [];
    let completedProcesses = [];

    let workHistory = [];

    for (let i = 0; i <= algorithms.maxTime; i++) {

      for (const arrivedProcess of arrivedProcesses) {
        arrivedProcess.wait();
      }

      if (chosenProcess) {
        chosenProcess.doWork();
        workHistory.push(chosenProcess.index);
      } else {
        workHistory.push(-1);
      }


      if (chosenProcess && chosenProcess.duration === 0) {
        completedProcesses.push(chosenProcess);
      }

      if (processes.length === 0 && arrivedProcesses.length === 0 && chosenProcess && chosenProcess.duration === 0) {
        break;
      }

      let len = processes.length;
      for (let j = 0; j < len; j++) {
        if (processes[0].arrivalTime === i) {
          arrivedProcesses.push(processes[0]);
          processes.shift();

          if (chosenProcess && arrivedProcesses[arrivedProcesses.length - 1].duration < chosenProcess.duration) {
            let temp = arrivedProcesses.pop();
            arrivedProcesses.push(chosenProcess);
            chosenProcess = temp;
          }

          len--;
        } else {
          break;
        }
      }


      if (!chosenProcess || chosenProcess && chosenProcess.duration === 0) {
        if (arrivedProcesses.length > 0) {
          arrivedProcesses.sort((a,b) => (a.duration > b.duration) ? 1 : ((b.duration > a.duration) ? -1 : 0));
          chosenProcess = arrivedProcesses[0];
          arrivedProcesses.shift();
        } else {
          chosenProcess = undefined;
        }
      }

    }

    return {
      name: 'SRTF',
      workHistory: workHistory,
      avgWaitTime: completedProcesses.map(value => value.waitingTime)
        .reduce((previousValue, currentValue) => previousValue + currentValue) / completedProcesses.length
    };
  },


  rr: (pattern) => {
    let quota = 4;
    let currentProcessQuota = 0;
    let processes = algorithms.decodePattern(pattern);

    let chosenProcess;
    let arrivedProcesses = [];
    let completedProcesses = [];

    let workHistory = [];

    for (let i = 0; i <= algorithms.maxTime; i++) {

      for (const arrivedProcess of arrivedProcesses) {
        arrivedProcess.wait();
      }

      if (chosenProcess) {
        chosenProcess.doWork();
        currentProcessQuota++;
        workHistory.push(chosenProcess.index);
      } else {
        workHistory.push(-1);
      }


      if (chosenProcess && chosenProcess.duration === 0) {
        completedProcesses.push(chosenProcess);
      }

      if (processes.length === 0 && arrivedProcesses.length === 0 && chosenProcess && chosenProcess.duration === 0) {
        break;
      }

      let len = processes.length;
      for (let j = 0; j < len; j++) {
        if (processes[0].arrivalTime === i) {
          arrivedProcesses.push(processes[0]);
          processes.shift();
          len--;
        } else {
          break;
        }
      }


      if (chosenProcess && currentProcessQuota === quota && chosenProcess.duration > 0) {
        arrivedProcesses.push(chosenProcess);
        chosenProcess = arrivedProcesses[0];
        currentProcessQuota = 0;
        arrivedProcesses.shift();
      }


      if (!chosenProcess || chosenProcess && chosenProcess.duration === 0) {
        if (arrivedProcesses.length > 0) {
          chosenProcess = arrivedProcesses[0];
          currentProcessQuota = 0;
          arrivedProcesses.shift();
        } else {
          chosenProcess = undefined;
        }
      }

    }

    return {
      name: 'RR4',
      workHistory: workHistory,
      avgWaitTime: completedProcesses.map(value => value.waitingTime)
        .reduce((previousValue, currentValue) => previousValue + currentValue) / completedProcesses.length
    };
  },


  fcfs2: (pattern) => {
    let processes = algorithms.decodePattern(pattern);

    let chosenProcess;
    let arrivedProcesses = [];
    let completedProcesses = [];

    let workHistory = [];

    for (let i = 0; i <= algorithms.maxTime; i++) {

      for (const arrivedProcess of arrivedProcesses) {
        arrivedProcess.wait();
      }

      if (chosenProcess) {
        chosenProcess.doWork();
        workHistory.push(chosenProcess.index);
      } else {
        workHistory.push(-1);
      }


      if (chosenProcess && chosenProcess.duration === 0) {
        completedProcesses.push(chosenProcess);
      }

      if (processes.length === 0 && arrivedProcesses.length === 0 && chosenProcess && chosenProcess.duration === 0) {
        break;
      }

      if (processes.length > 0 && processes[0].arrivalTime <= i) {
        arrivedProcesses.push(processes[0]);
        processes.shift();
      }


      if (!chosenProcess || chosenProcess && chosenProcess.duration === 0) {
        if (arrivedProcesses.length > 0) {
          chosenProcess = arrivedProcesses[0];
          arrivedProcesses.shift();
        } else {
          chosenProcess = undefined;
        }
      }

    }

    return {
      name: 'FCFS',
      workHistory: workHistory,
      avgWaitTime: completedProcesses.map(value => value.waitingTime)
        .reduce((previousValue, currentValue) => previousValue + currentValue) / completedProcesses.length
    };
  }

};

export default algorithms;
