<template>
  <v-app>
    <v-app-bar app>
      <v-toolbar-title class="headline">
        <span>Protsessoriaja planeerimise algoritmid</span>
      </v-toolbar-title>
    </v-app-bar>

    <v-snackbar
      :timeout="4000"
      :value="error"
      color="error"
      top
    >
      {{ errorMessage }}
    </v-snackbar>

    <v-content>
      <v-container fluid style="max-width: 100%" class="ma-0">
        <v-row>
          <v-col cols="2" dense>
            <v-row
              v-for="n in algorithms.length"
              :key="n"

            >
              <v-col>
                <v-card width="100%"
                        :flat="selectedAlgorithm !== n - 1"
                        :raised="selectedAlgorithm === n - 1"
                        @click="select(n)"
                >
                  <div class="pt-4 pb-4" style="text-align: center">{{algorithms[n - 1].label}}</div>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="9" dense>
            <v-row justify="center">
              <v-col>
                <v-card>
                  <v-row>
                    <v-col>
                      <v-card-title class="ml-2">Vali v천i sisesta j채rjend</v-card-title>

                    </v-col>

                  </v-row>

                  <v-divider class="ml-4 mr-4"></v-divider>
                  <v-radio-group v-model="radioGroup" style="width: 100%">
                    <v-row
                      class="ma-2"
                      v-for="n in predefinedPatterns"
                      :key="n.label"

                    >
                      <v-col cols="4">
                        <v-radio
                          :value="n.pattern"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            {{n.label}}
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="6">
                      <span>
                        {{n.pattern}}
                      </span>
                      </v-col>
                    </v-row>
                    <v-row
                      class="ma-2 mb-0"

                    >
                      <v-col cols="4">
                        <v-radio
                          value="custom"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            Enda oma
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="6">
                        <v-text-field class="ma-0 pa-0" v-model="customPattern">

                        </v-text-field>
                      </v-col>
                    </v-row>
                  </v-radio-group>

                  <v-row no-gutters class="pb-4 mr-4">
                    <v-spacer></v-spacer>
                    <v-col cols="2" >
                      <v-btn color="green" text @click="save" outlined>Salvesta</v-btn>
                    </v-col>
                  </v-row>

                </v-card>
              </v-col>
            </v-row>
          </v-col>

        </v-row>

        <v-row justify="center">
          <v-col align="center" ref="canvasWrapper" style="height:100px">
            <canvas ref="drawing"></canvas>
          </v-col>
        </v-row>
        <v-row v-if="result">
          <v-col>
            <p>Keskmine ooteaeg: {{ formattedAvgTime }}</p>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import algorithms from './plugins/algorithms';

  export default {
    name: 'App',
    data: () => ({
      result: undefined,
      radioGroup: undefined,

      error: false,
      errorMessage: "",

      colors: ["#206F71",
        "#8BC9AE",
        "#FCEBA9",
        "#FCB06F",
        "#D94B50"],
      colorIndex: 0,

      algorithms: [{label: "FSFC", func: algorithms.fcfs}, {label: "SRTF", func: algorithms.srtf}, {label: "RR-4", func: algorithms.rr}, {label: "2 level FCFS", func: algorithms.fcfs}],

      customPattern: "",

      selectedAlgorithm: 0,

      predefinedPatterns: [
        {
          label: "Esimene",
          pattern: "0,7;1,5;2,3;3,1;4,2;5,1"
        },
        {
          label: "Teine",
          pattern: "0,2;0,4;12,4;15,5;21,10"
        },
        {
          label: "Kolmas",
          pattern: "1,10;3,3;4,1;8,6;15,2"
        },
      ]
    }),

    methods: {
      save: function () {
        try {
          let pattern;
          if (this.radioGroup === "custom") {
            pattern = this.customPattern;
          } else {
            pattern = this.radioGroup;
          }
          this.result = this.algorithms[this.selectedAlgorithm].func(pattern);
          console.log(this.result.workHistory);
        } catch (e) {
          this.error = true;
          this.errorMessage = e;
          return;
        }

        this.drawHistory(this.result.workHistory);
      },

      drawHistory: function (workHistory) {
        let canvas = this.$refs["drawing"];
        let canvasWrapper = this.$refs["canvasWrapper"];
        canvas.width = canvasWrapper.clientWidth - 24;
        canvas.height = canvasWrapper.clientHeight - 24;

        const ctx = canvas.getContext("2d");

        let len = workHistory.length - 1;
        let rectWidth = (canvas.width - 20) / len;

        let i;
        for (i = 1; i < workHistory.length; i++) {
          let workedProcess = workHistory[i];

          let j;
          for (j = i + 1; j < workHistory.length; j++) {
            if (workHistory[j] !== workedProcess) {

              break;
            }
          }
          j--;
          if (workedProcess === -1) {
            ctx.fillStyle = "white";
          } else {
            let color = this.colors[this.colorIndex];
            ctx.fillStyle = color;
            this.colorIndex = (this.colorIndex + 1) % this.colors.length;
          }

          let x1 = rectWidth * (i - 1);
          let y1 = 0;
          let x2 = rectWidth * j;
          let y2 = canvas.height - 10;
          ctx.fillRect(x1, y1, x2, y2);

          let processLabel;

          if (workedProcess === -1) {
            processLabel = "";
          } else {
            processLabel = "P" + workedProcess;
          }
          ctx.fillStyle = "black";
          ctx.font = "20px Arial";
          ctx.fillText(processLabel, (rectWidth * (i - 1) + rectWidth * j) / 2 - 10, canvas.height / 2);
          ctx.font = "10px Arial";
          if (i === 1) {
            ctx.fillText("0", 0, canvas.height - 2);
          }

          ctx.fillText(j, j * rectWidth, canvas.height - 2);
          i = j;

        }

        ctx.fillStyle = "white";


        let x1 = rectWidth * (i - 1);
        let y1 = 0;
        let x2 = canvas.width;
        let y2 = canvas.height - 10;
        ctx.fillRect(x1, y1, x2, y2);

      },

      select: function(n) {
        if (n !== 4) {

          this.selectedAlgorithm = n - 1
        }
      }
    },

    computed: {
      formattedAvgTime: function () {
        return this.result ? parseFloat(Math.round(this.result.avgWaitTime * 100) / 100).toFixed(2) : undefined;
      }
    },

    created: function () {
      this.radioGroup = this.predefinedPatterns[0].pattern;
    },

    watch: {

    }

  };
</script>

<style>
  .v-input__control {
    width: 100% !important;
  }
</style>
